import Layout from '../components/Layout';
// import Image from '../components/Image';
import SEO from '../components/SEO';

import styles from './index.module.scss';

const IndexPage = () => (
  <Layout noHeader>
    <SEO title="METAKRONISMS" />

    <div className={styles.root}>
      <div className={styles.title}>
        Incoming transmission
        <br />
        Stand by
      </div>

      <svg
        className={styles.svg}
        width="823"
        height="411"
        viewBox="0 0 823 411"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
      >
        <g className={styles.circles}>

          <circle
            className={styles.pulsatingCircle}
            cx="411.5"
            cy="205.5"
            stroke="#1ee61e"
            fill="none"
            r="102.25"
            strokeWidth="1"
          />
          <g
            className={[
              styles.pulsatingCircle,
              styles.pulsatingCircle__1,
            ].join(' ')}
          >
            <circle
              cx="411.5"
              cy="205.5"
              stroke="#1ee61e"
              fill="none"
              r="105"
              strokeWidth="10"
              strokeDasharray="30 80"
            />
          </g>
          <circle
            className={[
              styles.pulsatingCircle,
              styles.pulsatingCircle__2,
            ].join(' ')}
            cx="411.5"
            cy="205.5"
            stroke="#1ee61e"
            fill="none"
            r="102.25"
            strokeWidth="1"
          />
        </g>
      </svg>
    </div>
  </Layout>
);

